:root {
  --c-bg: #000;
  --c-text: #f2f2f2;
  --fs-text: 14px;
  --fs-heading: 18px;
  --fs-title: 60px;
  --column: 90px;
  --margin: 80px;
}

*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  min-height: 100vh;
  width: 100vw;
  color: var(--c-text);
  font-family: Cinzel, serif;
  font-weight: 400;
  font-size: var(--fs-text);
  font-feature-settings: "liga";
  text-size-adjust: none;
  -webkit-font-smoothing: antialiased;
  background: url("bg-projects.fd533cd0.png") center / cover no-repeat fixed;
  display: block;
}

div {
  display: block;
}

.pageHeader {
  z-index: 2;
  width: 100%;
  height: calc(var(--fs-title)  + .8rem);
  padding-top: 10px;
  padding-right: var(--margin);
  box-sizing: content-box;
  border-bottom: 1px solid gray;
  justify-content: space-between;
  align-items: baseline;
  column-gap: 1rem;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.pageHeader .title {
  align-items: baseline;
  column-gap: 1rem;
  padding-left: 110px;
  display: flex;
}

.pageHeader .title .label {
  font-size: var(--fs-heading);
}

.pageHeader .title .name {
  font-size: var(--fs-title);
}

.vertical_line {
  height: 100%;
  width: 1px;
  background-color: gray;
  position: fixed;
  left: 60px;
}

.siteHeader {
  z-index: 2;
  position: fixed;
  top: 650px;
  left: 110px;
}

.siteHeader_title {
  letter-spacing: -.02em;
  cursor: crosshair;
  font-family: Cinzel, serif;
  font-size: 15px;
  line-height: .9;
}

.siteHeader_title a {
  color: var(--c-text);
  cursor: crosshair;
  text-decoration: none;
}

.siteHeader_title .line3 {
  text-align: right;
}

img {
  height: 22rem;
  border: 1px solid gray;
  border-radius: .1rem;
  position: fixed;
  top: 100px;
  left: 110px;
}

.about_inner {
  z-index: 1;
  position: absolute;
  top: 13rem;
  right: 6rem;
}

.aboutItem {
  width: 550px;
  padding: 0 0 6rem;
  position: relative;
}

.one .index, .two .index {
  font-family: Cinzel, serif;
  font-size: 15px;
  font-weight: 600;
  position: absolute;
  top: 5px;
  left: -250px;
}

p {
  padding-bottom: 1rem;
  font-family: Noto Serif JP, serif;
  font-size: 16px;
}

ul {
  flex-flow: wrap;
  gap: 1rem 1.5rem;
  display: flex;
}

.footerCopyright {
  padding-bottom: 3.5rem;
  position: absolute;
  bottom: -600px;
  left: 430px;
}

.footerCopyright .en {
  letter-spacing: -.04rem;
  opacity: .5;
  font-family: Marcellus, serif;
  font-size: 60px;
  font-weight: 400;
  line-height: 1.1;
}

@media only screen and (max-width: 1280px) {
  img {
    height: 20rem;
  }

  .about_inner {
    right: 5rem;
  }

  .aboutItem {
    width: 450px;
  }

  .one .index, .two .index {
    font-size: 15px;
    left: -150px;
  }

  .footerCopyright .en {
    font-size: 45px;
  }
}

@media only screen and (max-width: 1038px) {
  .title {
    padding-top: 1rem;
  }

  .pageHeader .title .label {
    font-size: 15px;
  }

  .pageHeader .title .name {
    font-size: 45px;
  }

  img {
    height: 15rem;
  }

  .about_inner {
    right: 5rem;
  }

  .aboutItem {
    width: 350px;
  }

  .footerCopyright {
    padding-bottom: 3.5rem;
    position: absolute;
    bottom: -600px;
    left: 450px;
  }

  .footerCopyright .en {
    font-size: 35px;
  }
}

@media only screen and (max-width: 895px) {
  .about_inner {
    right: 5rem;
  }

  .one .index {
    font-size: 15px;
    top: -50px;
    left: 0;
  }

  .two .index {
    left: 0;
  }

  ul {
    flex-direction: column;
    align-items: flex-end;
    padding-right: 2rem;
  }

  .footerCopyright {
    padding-bottom: 3.5rem;
    position: absolute;
    bottom: -600px;
    left: 350px;
  }

  .footerCopyright .en {
    font-size: 30px;
  }
}

@media only screen and (max-width: 764px) {
  .pageHeader .title .label {
    font-size: var(--fs-heading);
  }

  .pageHeader .title .name {
    font-size: 45px;
  }

  .about_inner {
    right: 5rem;
  }

  .one .index, .two .index {
    font-size: 14px;
  }

  .footerCopyright {
    padding-bottom: 3.5rem;
    position: absolute;
    bottom: -500px;
    left: 350px;
  }

  .footerCopyright .en {
    font-size: 25px;
  }
}

@media only screen and (max-width: 732px) {
  .pageHeader {
    position: absolute;
  }

  .pageHeader .title .label {
    font-size: 20px;
  }

  .pageHeader .title .name {
    font-size: 40px;
  }

  .siteHeader_title {
    font-size: 14px;
  }

  .siteHeader {
    display: none;
  }

  img {
    height: 28rem;
    position: absolute;
  }

  .about_inner {
    top: 650px;
    left: 110px;
  }

  .one .index {
    font-size: 15px;
    top: -50px;
    left: 0;
  }

  .two .index {
    left: 0;
  }

  ul {
    flex-direction: column;
    align-items: flex-end;
    padding-right: 2rem;
  }

  .footerCopyright {
    padding-bottom: 3.5rem;
    position: absolute;
    bottom: -950px;
    left: 110px;
  }

  .footerCopyright .en {
    font-size: 25px;
  }
}

@media only screen and (max-width: 375px) {
  .pageHeader .title {
    padding-left: 40px;
  }

  .siteHeader, .vertical_line {
    display: none;
  }

  img, .about_inner {
    left: 40px;
  }

  .footerCopyright {
    bottom: -1050px;
    left: 40px;
  }
}

